.container-port {
    position: relative;
    width: 50%;
}

.imgHover {
    opacity: 1;
    display: block;
    width: 100%;
    height: 100%;
    transition: 0.5s ease;
    backface-visibility: hidden;
}

.lo-txt {
    color: #fff;
    z-index: 99999;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    bottom: 23px;
    font-size: 30px;
    font-family: system-ui !important;
}

.hideImgHover {
    transition: 0.5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    color: #ffffff;
}

.container-port:hover .imgHover {
    opacity: 0.3;
}

.container-port:hover .hideImgHover {
    opacity: 1;
}

.container-port:hover .hideImgHover h3 {
    color: #ffffff;
}

