  top: 0;
  transition-duration: 0.4s;
}

.b .btn-6::after {
  left: 50%;
  bottom: 0;
  transition-duration: 0.4s;
}

.b .btn-6 span::before {
  left: 0;
  top: 50%;
  transition-duration: 0.4s;
}

.b .btn-6 span::after {
  right: 0;
  top: 50%;
  transition-duration: 0.4s;
}

.b span::before,
.b span::after {
  content: "";
  width: 4px;
  height: 0;
  position: absolute;
  transition: all 0.2s linear;
  background: #08adc3;
}

.b button::before,
.b button::after {
  content: "";
  width: 0;
  height: 4px;
  position: absolute;
  transition: all 0.2s linear;
  background: #08adc3;
}

.b span {
  display: block;
}

